body {
  text-align: center;
  color: #dddddd;
  text-shadow: 2px 2px rgba(0, 0, 0, 0.5);
  background-color: #666666;
}

.App-header {
  padding: 5px;
  color: #AF8C86;
  background-color: #36312D;
  background: -webkit-gradient(
      linear,
      left top, left bottom,
      from(rgba(0, 0, 0, 0.65)),
      to(rgba(0, 0, 0, 0.65))
      );
  background: -webkit-linear-gradient(
      rgba(0, 0, 0, 0.65),
      rgba(0, 0, 0, 0.65)
      );
  background: -o-linear-gradient(
      rgba(0, 0, 0, 0.65),
      rgba(0, 0, 0, 0.65)
      );
  background: linear-gradient(
      rgba(0, 0, 0, 0.65),
      rgba(0, 0, 0, 0.65)
      );
}

.App-title {
  font-size: 2em;
}

.App > p {
  width: 80vw;
  text-align: center;
  margin: 0 auto 1vw;
}

.App {
  background-image: -webkit-gradient(
      linear,
      left top, left bottom,
      from(rgba(0, 0, 0, 0.5)),
      to(rgba(0, 0, 0, 0.5))
    ), url('webpic.jpg');
  background-image: -webkit-linear-gradient(
      rgba(0, 0, 0, 0.5),
      rgba(0, 0, 0, 0.5)
    ), url('webpic.jpg');
  background-image: -o-linear-gradient(
      rgba(0, 0, 0, 0.5),
      rgba(0, 0, 0, 0.5)
    ), url('webpic.jpg');
  background-image: linear-gradient(
      rgba(0, 0, 0, 0.5),
      rgba(0, 0, 0, 0.5)
    ), url('webpic.jpg');
  height: 100%;
  min-height: 100vw;
  background-size: cover;
}

.intro {
  line-height: 1.5em;
  font-size: 115%;
}



